import { ClassfyList, GoodsList } from 'api/classfy'
import { ERR_CODE } from 'api/config'
import List from 'components/list/list'
import NoData from 'base/noData/noData'
import Scroll from 'base/scroll/scroll'
import Tab from 'base/tab/tab'
import TabItem from 'base/tab/tab-item'

export default {
	data() {
		return {
			selected: "1",
			activeIndex: 0,

			classfyList: [],
			cagegory_id: '',
			type: '',

			jacketPage: 0,
			jacketAllLoaded: false,
			jacketLoading: true,
			jacketTopStatus: '',
			list: [],

			isData: false,

		}
	},
	methods: {
		changeType() {
			this.type = this.selected;
			this.jacketPage = 0;
			this.list = [];
			this.isData = false;
			this.getClassfyList();
		},
		getClassfyList() {
			let options = {
				type: Number(this.selected),
				pageSize: 100,
				currentPage: 0
			}
			ClassfyList(options).then(res => {
				if(res.data.code === ERR_CODE) {
					this.classfyList = res.data.data;
					if(res.data.data.length === 0)return;
					this.cagegory_id = res.data.data[0].cagegory_id;
					this.getJacket();
				}
			})
		},

		loadJacketBottom() {
			if(this.isData) {
				return;
			}
			this.jacketPage++;
			this.getJacket();
		},
		jacketTopChange(status) {
			this.jacketTopStatus = status;
		},
		getJacket() {
			let options = {
				pageSize: 10,
				currentPage: this.jacketPage,
				category_id: this.cagegory_id
			}
			if(this.type != '' && this.type != null) {
				options.type = this.type;
			}
			GoodsList(options).then(res => {
				if(res.data.code === ERR_CODE) {
					if(res.data.data === null) {
						this.isData = true;
						return;
					}
					this.list = this.list.concat(res.data.data);
				}
			})
		},

		nav(item, index) {
			this.cagegory_id = item.cagegory_id;
			this.jacketPage = 0;
			this.list = [];
			this.isData = false;
			this.activeIndex = index;
			this.getJacket();
		},
		goDetail(val) {
			this.$router.push({
				name: `detail`,
				query: {
					id: val.goods_id
				}
			})
		}
	},
	created() {
		if(this.$route.query.type != undefined){
		this.selected = this.$route.query.type + '';
		}else{
			this.selected = '1';
		}

		this.changeType();
	},
	mounted() {
	},
	components: {
		List,
		NoData,
		Scroll,
		TabItem,
		Tab

	}
}



