export default {
	props: {
		status: {
			type: String,
			default: ''
		},
		isData: {
			type: Boolean,
			default: false,
		},
		noLoading: {
			type: Boolean,
			default: true,
		}
	},
	data() {
		return {
			isStatus: '',
		}
	},
	watch: {
		status(val) {


			console.log(val);
			this.isStatus = val;
		}
	}
}



