/**
 * ly-tab-item
 * @desc 搭配 tab 使用
 * @param {Number} id - 选中的item的索引值
 * @param {slot} [icon] - icon 图标
 * @param {slot} - 文字
 *
 * @example
 * <ly-tab-item>
 *   <img slot="icon" src="http://placehold.it/100x100">
 *   前端
 * </ly-tab-item>
 */
export default {
	name: 'LyTabItem',
	computed: {
		activeStyle() {
			return {
				color: '#4c3622',
				borderColor: '#4c3622',
				borderWidth: '3px',
				borderBottomStyle: 'solid'
			}
		}
	},
	data() {
		return {
			id: (this.$parent.$children.length || 1) - 1
		}
	},
	methods: {
		onItemClicked() {
			this.$parent.$emit('input', this.id)
		}
	}
}



